<template lang="pug">
    .scholars__content
        .scholars__title Ученики
        .scholars__filter
            .scholars__filter-item
                label.label__filter Выберите класс:
                select( v-model="selectedClass").scholars__filter-select
                    option(value=0) Все
                    option(:value='item' v-for='item in 12') {{item}}
            .scholars__filter-item
                label.label__filter Выберите группу:
                select( v-model="selectedGroupId").scholars__filter-select
                    option(value=0) Все
                    option(:value='group.id' v-for='group in filteredGroups') {{group.code}}
            .scholars__filter-item
                label.label__filter Выберите ученика:
                select( v-model="selectedScholarId").scholars__filter-select 
                    option(value=0) Все
                    option(:value='scholar.id' v-for='scholar in filteredScholars') {{scholar.lastName}} {{scholar.firstName}}
        .scholars__container
            ul.scholars__list
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.scholars__item(v-for="scholar,idx in filteredScholars" :key="scholar.id" v-if='(item-1)*steps <= idx && idx< item*steps')
                            scholar-item(
                                :index='idx'
                                :scholar='scholar'
                            )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")
        button(@click.prevent='addScholar' id='addScholar').add__scholar Добавить ученика
        add-scholar-form(
            v-if='addScholarFormVisible'
            v-on:eventNewScholar='eventNewScholar'
            )
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
export default {
    components: {
        addScholarForm: () => import("../../admin/scholars/add-scholar-form"),
        scholarItem: () => import("../../admin/scholars/scholar-item"),
        Swiper,
        SwiperSlide
    },
    created() {
        this.fetchGroups(),
        this.fetchScholars()
    },
    data () {
        return {
            steps: 4,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            addScholarFormVisible: false,
            selectedGroupId: 0,
            selectedSubjectId: 0,
            selectedClass: 0,
            selectedScholarId: 0
        }
  }, 
  computed: {
        ...mapState ("scholars", {
            scholars: state => state.scholars}),
        ...mapState ("groups", {
            groups: state => state.groups}),
            filteredGroups() {
                return this.groups
                //filter by class
                .filter(ele=>this.selectedClass==0 || ele.groupClass==this.selectedClass)
            },
            filteredScholars() {
                return this.scholars
                //filter by class
                .filter(ele=>this.selectedClass==0 || ele.scholarClass==this.selectedClass)
                //filter by id
                .filter(ele=>this.selectedScholarId==0 || ele.id==this.selectedScholarId)
            },
            slidesNumber: function(){
                return (Math.floor(this.filteredScholars.length/this.steps)==this.filteredScholars.length/this.steps)?Math.floor(this.filteredScholars.length/this.steps):Math.floor(this.filteredScholars.length/this.steps)+1
        }
    },    
  methods: {
      ...mapActions("scholars", ["fetchScholars"]),
      ...mapActions("scholars", ["fetchScholarDepartments"]),
      ...mapActions("groups", ["fetchGroups"]),
      ...mapActions("departments", ["fetchDepartments", "fetchDepartmentScholars"]),
      addScholar() {
          this.addScholarFormVisible=!this.addScholarFormVisible
          document.getElementById('addScholar').setAttribute("disabled", "true")
      },
      eventNewScholar() {
          this.addScholarFormVisible=!this.addScholarFormVisible
          document.getElementById('addScholar').removeAttribute("disabled")
      }
  }
}
</script>
